$("._show-player").click(function () {
    $(".player").addClass("_active")
})

$(".player-control__play").click(function () {
    $(this).toggleClass("_active")
})

$("._show-settings").click(function () {
    $(".player-settings").addClass("_active")
})


$("._show-series").click(function () {
    $(".series").addClass("_active")
})
$(".series__close").click(function () {
    $(".series").removeClass("_active")
})


$(".series__tab").click(function () {
    let tab = $(this).attr("data-tab")
    console.log(tab)
    $(".series__tab").removeClass("_active")
    $(".series-group").removeClass("_active")
    $(this).addClass("_active")
    $(`.${tab}`).addClass("_active")
})

$(document).mouseup(function(e) {
    var div = $('.player-settings');
    if (!div.is(e.target) && div.has(e.target).length === 0) {
        if($(div).hasClass("_active")){
            $(div).removeClass("_active")
        }
    }
});
